import { Model } from '../base/Model';
export class AppData extends Model {
    constructor() {
        super(...arguments);
        this.basket = [];
        this.favorites = [];
        this.formErrors = {};
    }
    //   
    addBasket(item) {
        if (this.basket.indexOf(item) < 0) {
            this.basket.push(item);
            this.updateBasket();
        }
    }
    //   
    addFavorites(item) {
        if (!this.productLike(item)) {
            this.favorites.push(item);
            this.updateFavorites();
            this.saveFavoritesToLocalStorage();
        }
    }
    removeFromLike(id) {
        this.favorites = this.favorites.filter((it) => it.id !== id);
        this.saveFavoritesToLocalStorage();
        this.emitChanges('items:changed');
    }
    productLike(item) {
        return Boolean(this.favorites.find((liketItem) => liketItem.id === item.id));
    }
    saveFavoritesToLocalStorage() {
        try {
            localStorage.setItem('favorites', JSON.stringify(this.favorites));
        }
        catch (e) {
            console.warn('   favorites  localStorage', e);
        }
    }
    loadFavoritesFromLocalStorage() {
        try {
            const data = localStorage.getItem('favorites');
            if (data) {
                this.favorites = JSON.parse(data);
            }
        }
        catch (e) {
            console.warn('   favorites  localStorage', e);
            this.favorites = [];
        }
    }
    //,     .
    productOrder(item) {
        return Boolean(this.basket.find((basketItem) => basketItem.id === item.id));
    }
    //   
    clearBasket() {
        this.basket = [];
        this.updateBasket();
    }
    // 
    updateBasket() {
        this.emitChanges('basket:changed', this.basket); //  
    }
    updateFavorites() {
        this.emitChanges('items:changed', this.favorites); //  
    }
    //   
    removeFromBasket(id) {
        this.basket = this.basket.filter((it) => it.id !== id);
        this.emitChanges('basket:changed');
    }
    //   
    //   
    getOrderProducts() {
        return this.basket;
    }
    getFavoritesProducts() {
        return this.favorites;
    }
    //  
    getTotalPrice() {
        return this.basket.reduce((total, item) => {
            return total + item.price;
        }, 0);
    }
    //     
    setCatalog(item) {
        this.items = item;
        this.emitChanges('items:changed', { catalog: this.items });
    }
    setPreview(item) {
        this.preview = item.id;
        this.emitChanges('preview:changed', item);
    }
}
